import type {CSRFResponse} from '../types';

import getBaseInternURL from './getBaseInternURL';

const CSRF_URL = getBaseInternURL() + '/api/dtsg/internal';
const CSRF_LOCALSTORAGE_KEY = 'workappusage:csrf';

// memoize up to the expiry time (or fall back to 2hrs) so we only send one csrf request at a time
let _memoCSRF = null;
let _memoTime = null;

export default async function genCachedCSRFToken(): Promise<?string> {
  const now = Date.now() / 1000;
  if (_memoCSRF === null || _memoTime == null || _memoTime <= now) {
    _memoCSRF = await genCSRFToken();
    _memoTime = _memoCSRF?.expire ?? now + 7200;
  }
  return _memoCSRF?.token;
}

async function genCSRFToken(): Promise<?CSRFResponse> {
  const item = await genLocalStorageItem(CSRF_LOCALSTORAGE_KEY);
  const now = Date.now() / 1000 + 60;
  if (item != null && item.expire >= now) {
    return item;
  }
  const response = await fetch(CSRF_URL);
  if (response.status !== 200) {
    return null;
  }
  const text = await response.text();
  if (text == null) {
    return null;
  }
  const token = (JSON.parse(text.slice(9)): CSRFResponse);
  setLocalStorageItem(CSRF_LOCALSTORAGE_KEY, token);
  return token;
}

async function genLocalStorageItem(key: string): Promise<CSRFResponse> {
  const items = await chrome.storage.local.get(key);
  if (chrome.runtime.lastError) {
    throw Error(chrome.runtime.lastError.message);
  }
  return (items[key]: CSRFResponse);
}

function setLocalStorageItem(key: string, value: CSRFResponse) {
  chrome.storage.local.set({
    [key]: value,
  });
}
