import * as ods from "./ods";

const DEBUG = process.env.NODE_ENV === 'development';

export function info(event: string, ...args: Array<mixed>) {
  DEBUG && console.log(event, ...args);
}

export function warn(event: string, ...args: Array<mixed>) {
  DEBUG && console.warn(event, ...args);
}

export function mustfix(event: string, ...args: Array<mixed>) {
  DEBUG && console.error(event, ...args);
  ods.bumpError(event, 1)
}
