import type {CSRFResponse, Messages} from './types';

import validateURLForInternalFB404 from './validateURLForInternalFB404';
import genLog from './logging/genLog';
import {TSEvent} from './types';
import * as InternalLogger from './logging/InternalLogger';
import * as ods from './logging/ods';
import {
  getCheckIfIsNotMetaAccount,
  validateURLIsGoogleDomain,
} from './utils/GoogleDomainUtils';

const tabIDToData: {
  [id: number]: ?{
    url: ?string,
    isInternalFB404: boolean,
    isNotMetaAccount?: boolean,
  },
} = {};

/**
 * Fired when the active tab on the window changes
 */
chrome.tabs.onActivated.addListener(async function (activeInfo) {
  const activedTab = await chrome.tabs.get(activeInfo.tabId);
  const url = activedTab?.url;
  if (url) {
    const isInternalFB404 = validateURLForInternalFB404(
      activedTab?.title,
      activedTab?.url,
    );
    const isNotMetaAccount = await genCheckIfIsNotMetaAccount(
      activeInfo.tabId,
      url,
    );

    tabIDToData[activeInfo.tabId] = {
      url,
      isInternalFB404,
      isNotMetaAccount: isNotMetaAccount === true,
    };
    if (isNotMetaAccount) {
      return;
    }
    genLog(TSEvent.BROWSER_TAB_ACTIVATE, url, isInternalFB404);
  }
});

/**
 * Fired on any updates to the tab, including updates to the URL
 */
chrome.tabs.onUpdated.addListener(async function (tabId, changeInfo, tab) {
  tabIDToData[tabId] = {
    url: tab.url,
    isInternalFB404: validateURLForInternalFB404(tab.title, tab.url),
    isNotMetaAccount: await genCheckIfIsNotMetaAccount(tabId, tab.url),
  };
});

/**
 * Fired when a tab is closed
 */
chrome.tabs.onRemoved.addListener(async function (tabId, removeInfo) {
  const data = tabIDToData[tabId];
  if (data?.url && data?.isNotMetaAccount !== true) {
    genLog(TSEvent.BROWSER_TAB_REMOVE, data.url, data.isInternalFB404);
  }
  delete tabIDToData[tabId];
});

chrome.runtime.onMessage.addListener<Messages>((message, sender) => {
  if (tabIDToData[sender.tab.id]?.isNotMetaAccount === true) {
    return;
  }

  if (message.type === 'work_app_usage.log_request') {
    const {event, isInternalFB404, ts} = message.params;
    genLog(event, sender.tab.url, isInternalFB404, ts);
  }
});

chrome.alarms.onAlarm.addListener(alarm => {
  if (alarm.name == 'ods') {
    ods.sendToODS();
  }
});

// Signals should not be sent to ODS more often than once a minute:
// https://fb.workplace.com/groups/ods.users/permalink/1698146840222347/
chrome.alarms.create('ods', {
  delayInMinutes: 1,
  periodInMinutes: 2,
});

/**
 * Install the content scripts specified in the manifest on each open tab.
 * This also works on extension updates, where it injects the new script
 * into the existing tabs.
 */
function installContentScripts() {
  const manifest = chrome.runtime.getManifest();
  manifest.content_scripts.forEach(async contentScript => {
    const tabsFromQuery = await chrome.tabs.query({url: contentScript.matches});
    tabsFromQuery.forEach(async tab => {
      if (tab.url?.startsWith('chrome://')) {
        return;
      }

      const tabId = tab.id;
      if (tabId == null) {
        return;
      }

      // Tab isn't ready for a script
      if (tab.status === 'unloaded') {
        return;
      }

      try {
        await chrome.scripting.executeScript({
          target: {allFrames: true, tabId: tabId},
          files: contentScript.js,
        });
        const error = chrome.runtime.lastError;
        if (error != null) {
          InternalLogger.mustfix(
            'failed_to_install_content_script',
            tab,
            error.message,
          );
        }
        tabIDToData[tabId] = {
          url: tab.url,
          isInternalFB404: validateURLForInternalFB404(tab.title, tab.url),
          isNotMetaAccount: await genCheckIfIsNotMetaAccount(tabId, tab.url),
        };
      } catch (e) {
        InternalLogger.mustfix('failed_to_install_content_script', tab, e);
      }
    });
  });
}

async function genCheckIfIsNotMetaAccount(
  tabId: number,
  url: ?string,
): Promise<boolean> {
  if (url && validateURLIsGoogleDomain(url)) {
    const injectionResults = await chrome.scripting.executeScript({
      target: {tabId},
      func: getCheckIfIsNotMetaAccount,
    });

    return injectionResults.some(({result}) => result === true);
  }
  return false;
}

installContentScripts();

ods.bump('mwau.background_init', 'success', 1);

/**
 * Sets up a valid port, which allows content scripts setup a listener on onDisconnect.
 *
 * When this background worker is removed, this removes the listener for onConnect.
 * As a result, this fires the onDisconnect listener in the content scripts,
 * which alerts them to being orphaned.
 *
 * https://developer.chrome.com/docs/extensions/mv2/messaging/#port-lifetime
 */
chrome.runtime.onConnect.addListener(() => {});
