const composedAllowlistRegex = new RegExp(
  allowlistDomains.map(regex => regex.source).join('|'),
);

export function obfuscateURLIfDomainNotAllowlisted(url: string): {
  origin: string,
  url: string,
} {
  const {hostname, origin} = new URL(url);
  if (composedAllowlistRegex.test(hostname)) {
    return {url, origin};
  }
  return {url: 'http://--untracked--/', origin};
}
