'use strict';

const ODS_URL: string = 'https://graph.facebook.com/ods_metrics';

// https://www.internalfb.com/intern/ods/category?cat_id=6481
const CATEGORY_ID = '6481';

/**
 * Access token from https://developers.facebook.com/apps/630913892394846/
 *
 * https://developers.facebook.com/apps/630913892394846/settings/basic/
 */
const ACCESS_TOKEN: string = '630913892394846|9a367e1e5b8b1e4c9d58f4655c6ecec3';

let batch: {
  [entity: string]: {
    [key: string]: number,
  },
} = {};

export function bump(entity: string, key: string, value: number) {
  if (batch[entity] == null) {
    batch[entity] = {};
  }
  if (batch[entity][key] == null) {
    batch[entity][key] = value;
  } else {
    batch[entity][key] += value;
  }
}

export function bumpError(key: string, value: number) {
  bump('mwau.errors', key, value);
}

export async function sendToODS() {
  const localBatch = batch;
  batch = {};
  const datapoints = [];
  for (const entity in localBatch) {
    for (const key in localBatch[entity]) {
      datapoints.push({
        entity,
        key,
        value: localBatch[entity][key],
      })
    }
  }
  if (datapoints.length === 0) {
    return;
  }
  const formData: URLSearchParams = new URLSearchParams();
  formData.set('access_token', ACCESS_TOKEN);
  formData.set('category_id', CATEGORY_ID);
  formData.set(
    'datapoints',
    JSON.stringify(
      datapoints
    )
  );

  const result = await fetch(ODS_URL, {
    method: 'POST',
    body: formData,
    headers: {
      'Content-Type': 'application/json',
    },
  });
}
