import {GOOGLE_DOMAINS} from '../logging/URLObfuscator';

export function getCheckIfIsNotMetaAccount(): boolean {
  const nodeList = document.querySelectorAll(
    'a[aria-label^="Google Account:"][aria-label*="meta.com"],a[aria-label^="Google Account:"][aria-label*="fb.com"]',
  );
  return nodeList.length === 0;
}

export function validateURLIsGoogleDomain(url: string): boolean {
  const hostname = new URL(url).hostname;
  return GOOGLE_DOMAINS.test(hostname);
}
