import type {BatchedEventData, TSEvent} from '../types';

import * as InternalLogger from './InternalLogger';
import * as DomainCountTracker from './DomainCountTracker';
import {obfuscateURLIfDomainNotAllowlisted} from './URLObfuscator';
import genCachedCSRFToken from './genCachedCSRFToken';
import getBaseInternURL from './getBaseInternURL';

const LOGGING_URL = getBaseInternURL() + '/mwau/log_event/';
const VERSION = chrome.runtime.getManifest().version;

const MAX_RETRIES = 2;
const MAX_BATCHED_EVENTS = 200;
const MAX_DELAY_FOR_LOG = 1000; // 1 second

let batchedEvents: Array<BatchedEventData> = [];
let batchedEventsScheduledFn: ?TimeoutID = null;
let cachedInstanceID: ?string;

export default async function genLog(
  event: TSEvent,
  url: string,
  isInternalFB404: boolean,
  ts?: number,
) {
  if (url.startsWith('chrome://')) {
    return;
  }
  let obfuscationResult;
  try {
    obfuscationResult = obfuscateURLIfDomainNotAllowlisted(url);
  } catch (e) {
    const error = (e: Error);
    InternalLogger.mustfix('obfuscation_failed', url, error.toString());
    return;
  }

  const timestamp = ts ?? new Date().getTime() / 1000.0;
  const data = {
    event,
    isInternalFB404,
    url: obfuscationResult.url,
    ts: timestamp,
  };

  InternalLogger.info(`${String(event)}`, obfuscationResult.url);

  addEventToBatch(data);
  DomainCountTracker.bump(obfuscationResult.origin);
}

function addEventToBatch(data: BatchedEventData) {
  if (batchedEvents.length > MAX_BATCHED_EVENTS) {
    clearTimeout(batchedEventsScheduledFn);
    sendBatch();
  }
  if (batchedEventsScheduledFn == null) {
    batchedEventsScheduledFn = setTimeout(sendBatch, MAX_DELAY_FOR_LOG);
  }
  batchedEvents.push(data);
}

function sendBatch() {
  batchedEventsScheduledFn = null;
  genPostBatchedLogs(batchedEvents);
  batchedEvents = [];
}

async function genPostBatchedLogs(
  batchedEvents: Array<BatchedEventData>,
  retryCount: number = 0,
): Promise<mixed> {
  const [csrf, instanceID, _wait] = await Promise.all([
    genCachedCSRFToken(),
    getInstanceID(),
    wait(retryCount),
  ]);
  if (csrf == null) {
    InternalLogger.mustfix('missing_csrf_token');
    return;
  }

  const eventsByURL: {
    [string]: Array<{
      event: TSEvent,
      is_internalfb_404: boolean,
      timestamp: number,
    }>,
  } = {};
  for (const [idx, eventData] of batchedEvents.entries()) {
    if (!eventsByURL.hasOwnProperty(eventData.url)) {
      eventsByURL[eventData.url] = [];
    }
    eventsByURL[eventData.url].push({
      event: eventData.event,
      is_internalfb_404: eventData.isInternalFB404,
      timestamp: eventData.ts,
    });
  }
  const formData: URLSearchParams = new URLSearchParams();

  formData.set('version', VERSION);
  formData.set('events_by_url', JSON.stringify(eventsByURL));
  formData.set('instance_id', instanceID);
  formData.set('fb_dtsg', csrf);

  try {
    const result = await fetch(LOGGING_URL, {
      method: 'POST',
      body: formData,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    });

    switch (result.status) {
      case 200:
        return;
      case 403:
        InternalLogger.mustfix('unauthorized', batchedEvents);
        return;
      case 413:
        if (batchedEvents.length > 1) {
          InternalLogger.warn('batch_too_large_retry', batchedEvents);
          const midIdx = Math.ceil(batchedEvents.length / 2);
          const firstBatch = batchedEvents.slice(0, midIdx);
          const secondBatch = batchedEvents.slice(midIdx);
          genPostBatchedLogs(firstBatch);
          genPostBatchedLogs(secondBatch);
        } else {
          InternalLogger.mustfix('batch_too_large', batchedEvents);
        }
        return;
      case 500:
      case 503:
        if (retryCount < MAX_RETRIES) {
          InternalLogger.warn('server_error_retry', batchedEvents);
          genPostBatchedLogs(batchedEvents, retryCount + 1);
        } else {
          InternalLogger.mustfix('server_error', batchedEvents);
        }
        return;
      default:
        InternalLogger.mustfix('request_failed', result.status, batchedEvents);
        return;
    }
  } catch (e) {
    const error = (e: Error);
    InternalLogger.mustfix(
      'unknown_fetch_error',
      error.toString(),
      batchedEvents,
    );
  }
}

async function getInstanceID(): Promise<string> {
  if (cachedInstanceID == null) {
    cachedInstanceID = await chrome.instanceID.getID();
  }
  return cachedInstanceID;
}

async function wait(retryCount: number): Promise<void> {
  if (retryCount > 0) {
    return new Promise(resolve => setTimeout(resolve, 2 ** retryCount * 1000));
  }
  return;
}
