export enum TSEvent {
  BROWSER_TAB_ACTIVATE = 'browser_tab_activate',
  BROWSER_TAB_REMOVE = 'browser_tab_remove',
  BROWSER_WINDOW_FOCUS = 'browser_window_focus',
  BROWSER_WINDOW_BLUR = 'browser_window_blur',
  CLICK = 'click',
  KEYDOWN = 'keydown',
  MOUSEMOVE = 'mousemove',
  SCROLL = 'scroll',
}

export type CSRFResponse = {
  token: string,
  valid_for: number,
  expire: number,
};

export type Messages = {
  type: 'work_app_usage.log_request',
  params: {event: TSEvent, isInternalFB404: boolean, url: string, ts: number},
};

export type BatchedEventData = {
  event: TSEvent,
  isInternalFB404: boolean,
  url: string,
  ts: number,
};
